.room-container{
  background-image: url(https://images.pexels.com/photos/13787794/pexels-photo-13787794.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 2rem 10rem;
    gap: 4rem;
}

.room-container h1{
    font-size: 40px;
}

.room-grid{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10vw;
    cursor: pointer;
}

.room{
  background-color: rgb(255, 255, 255,0.5);
  backdrop-filter: blur(6px);
  box-shadow: 0 0rem 1rem 0 rgba(0,0,0,0.5);
  border-radius: 10px;
}

.room h4, p{
    display: flex;
    justify-content: center;
    align-items: center;
}

@media  screen and (max-width: 1500px){
  .room-container{
    background-image: url(https://images.pexels.com/photos/4818714/pexels-photo-4818714.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  }
}


@media  screen and (max-width: 1050px){
    .room-grid{
        grid-template-columns: repeat(2, 1fr);
    }
}

@media  screen and (max-width: 800px){
    .room-container{
        padding: 2rem 1rem;
    }
}

/* Animation */

.scale-in-center-room {
	-webkit-animation: scale-in-center 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) 5s both;
	        animation: scale-in-center 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) 5s both;
}
@-webkit-keyframes scale-in-center-room {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
    }
  }
  @keyframes scale-in-center {
    0% {
      -webkit-transform: scale(0);
              transform: scale(0);
      opacity: 1;
    }
    100% {
      -webkit-transform: scale(1);
              transform: scale(1);
      opacity: 1;
    }
  }

  .tracking-in-expand {
	-webkit-animation: tracking-in-expand 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) 5s both;
	        animation: tracking-in-expand 1s cubic-bezier(0.215, 0.610, 0.355, 1.000) 5s both;
}
@-webkit-keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }
  @keyframes tracking-in-expand {
    0% {
      letter-spacing: -0.5em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      opacity: 1;
    }
  }