.book-now{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 6rem;
}

.book-now h1 {
    display: flex;
    justify-content: center;
    padding: 0 4rem;
}

.book-btn {
    background: linear-gradient(216deg, #0E75B4 0%, #0393CA 50%, #002F99 100%);
    color: #ffff;
    font-size: 20px;
    font-weight: 1000;
    padding: 15px 30px;
    border-radius: 10px;
    margin-top: 2rem;
    border: none;
}



/* Animation */

.jello-book {
	animation: jello-book 0.9s infinite both;
}
@keyframes jello-book {
    0% {
      transform: scale3d(1, 1, 1);
    }
    30% {
      transform: scale3d(1.25, 0.75, 1);
    }
    40% {
      transform: scale3d(0.75, 1.25, 1);
    }
    50% {
      transform: scale3d(1.15, 0.85, 1);
    }
    65% {
      transform: scale3d(0.95, 1.05, 1);
    }
    75% {
      transform: scale3d(1.05, 0.95, 1);
    }
    100% {
      transform: scale3d(1, 1, 1);
    }
  }