.v-container{
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    padding: 6rem 0;
    margin: 4rem 4rem;
}

.left{
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img{
    border-radius: 3rem;
}

.right{
    width: 40%;
}

.right-header {
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    gap: 0.5rem;
}

.right-header p {
    justify-content:flex-start;
}

.accordion{
    border: none;
}

.accordion-btn {
    color: #fff;
    background-color: #1c1b1b;
    padding: 20px;
    border-radius: 10px;
    display: flex;
    justify-content:space-between;
    align-items: center;
}

@media  screen and (max-width: 900px) {
    .v-container {
      flex-direction: column;
      align-items: center;
      padding: 0;
    }

    .left{
        width: 100%;
        margin-bottom: 4rem;
    }

    .right {
        width: 100%;
        gap: 15px;
    }
}


/* Animation */

  .slide-in-bottom {
	-webkit-animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 8s both;
	        animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 8s both;
}
@-webkit-keyframes slide-in-bottom {
    0% {
      -webkit-transform: translateY(1000px);
              transform: translateY(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
  }
  @keyframes slide-in-bottom {
    0% {
      -webkit-transform: translateY(1000px);
              transform: translateY(1000px);
      opacity: 0;
    }
    100% {
      -webkit-transform: translateY(0);
              transform: translateY(0);
      opacity: 1;
    }
  }