.header{
    background-image: url(https://images.pexels.com/photos/2246476/pexels-photo-2246476.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    background-size: cover;
    backdrop-filter: blur(30px);
    padding: 20vh 8vw;
}

.header-container{
   display: flex;
   flex-direction: row;
   justify-content: space-between;
}

.header-left{
    display: flex;
    justify-content: center;
    align-items: center;
}

.header-left_text h1{
   font-size: 3em;
   color: #fff;
}

.header-left_text p{
    font-size: 1.6em;
    color: #fff;
}

.countup-container{
    padding: 2em 4vw;
}

.header-countup-box{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    gap: 2vw;
    color: #fff;
    font-size: 2em;
}


@media  screen and (max-width: 900px) {
    .header-container{
        flex-direction: column;
        gap: 3vh;
    }

    .header-left{
        justify-content: flex-start;
    }

    .header-left_text h1{
        font-size: 1.8em;
    }

    .header-left_text p{
        font-size: 1em;
    }

    .header-countup-box{
        font-size: 1em;
    }
}