.footer{
    width: 100%;
    font-size: 12px;
    padding: 6rem 6rem 1rem;
    background-color: #042e49;
  }
  
  .fLists{
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
  }
  
  .fList{
    list-style: none;
    padding: 0;
  }
  
  .fListItem{
    margin-bottom: 10px;
    color: #ffffff;
    cursor: pointer;
  }

  .rights{
    display: flex;
    justify-content: center;
    color: #ffffff;
  }

  @media  screen and (max-width: 900px) {
    .fLists {
        flex-direction: column;
        gap: 1rem;
    }

    .rights{
      margin-left: 0;
    }
  }