.search-container{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px;
    margin: 10vh 20vw;
}

.Search {
    position: absolute;
    height: 10vh;
    background-color: white;
    border: 3px solid #032988;
    display: flex;
    align-items: center;
    justify-content: space-around;
    border-radius: 20px;
    
    width: 100%;
    max-width: 1024px;
    z-index: 5;
  }
  
  .headerIcon {
    color: lightgray;
  }
  
  .SearchItem {
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
  }
  
  .SearchInput {
    border: none;
    outline: none;
  }

  .date{
    position: absolute;
    top: 8vh;
  }

  .Search-btn{
    color: #fff;
    background: linear-gradient(216deg, #0E75B4 0%, #0393CA 50%, #002F99 100%);
    padding: 15px 20px;
    border-radius: 10px;
    border: none;
  }

  @media  screen and (max-width: 900px) {
    .search-container{
      margin: 20vh 30vw;
      padding: 0;
    }
    
    .Search {
      height: 40vh;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      border-radius: 0px;
      padding: 20px;
      gap: 3rem;
    }

    .date{
      top: 20vh;
    }
  }

  /* Animation */

  .slide-in-left {
    animation: slide-in-left 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 2s both;
  }
  @keyframes slide-in-left {
    0% {
      transform: translateX(-1000px);
      opacity: 0;
    }
    100% {
      transform: translateX(0);
      opacity: 1;
    }
  }