.hero{
    background-image: url(https://images.pexels.com/photos/325185/pexels-photo-325185.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    background-size: cover;
    display: flex;
    text-align: center;
    flex-direction: column;
    }

    .mainText{
      font-weight: 1000;
      font-size: 25px;
      line-height: 2.5rem;
      margin-top: 4rem;
    }

    .mainText h1{
      color: #ffff;
      font-size: 4vw;
      letter-spacing: 4px;
    }

  
    .heroImgs{
      width: 100%;
      height: 1rem auto 0;
      justify-content: center;
      position: relative;
      border-radius: 5rem;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .videoDiv{
      width: 40%;
      display: flex;
      justify-content: center;
      align-items: center;
    }

    .video{
      width: 100%;
      height: 20%;
      border-radius: 50rem;
    }

    .image{
      position: absolute;
      width: 20%;
    }

  @media  screen and (max-width: 900px) {
    .hero{
      padding-bottom: 10vh;
    }

    .mainText h1{
      font-size: 8vw;
      top: 25;
    }

    .videoDiv {
      width: 70%;
    }
  }

  /* Animation */

.text-pop-up-top {
  animation: text-pop-up-top 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
@keyframes text-pop-up-top {
  0% {
    transform: translateY(0);
    transform-origin: 50% 50%;
    text-shadow: none;
  }
  100% {
    transform: translateY(-80px);
    transform-origin: 50% 50%;
    text-shadow: 0 1px 0 #969696, 0 2px 0 #969696, 0 3px 0 #969696, 0 4px 0 #969696, 0 5px 0 #969696, 0 6px 0 #969696, 0 7px 0 #969696, 0 8px 0 #969696, 0 9px 0 #969696, 0 50px 30px rgba(0, 0, 0, 0.3);
  }
}

.scale-in-center {
	animation: scale-in-center 1s cubic-bezier(0.250, 0.460, 0.450, 0.940) 1s both;
}
@keyframes scale-in-center {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

.puff-out-center {
	animation: puff-out-center 1s cubic-bezier(0.165, 0.840, 0.440, 1.000) infinite both;
}
@keyframes puff-out-center {
  0% {
    transform: scale(1);
    filter: blur(0px);
    opacity: 1;
  }
  100% {
    transform: scale(2);
    filter: blur(4px);
    opacity: 0;
  }
}
  
