
@import url('https://fonts.googleapis.com/css2?family=League+Spartan:wght@300;500&family=Montserrat:wght@100;500;700&family=Nunito+Sans:opsz,wght@6..12,1000&family=Rowdies:wght@700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
  scroll-behavior: smooth;
}

:root {
  --primary: #1f3e72;
  --secondary: rgba(255, 255, 255, 0.78);
  --black: #131110;
  --white: #ffff;
  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #f46767 102.67%);
  --blue-gradient: linear-gradient(97.05deg, #4066ff 3.76%, #2949c6 100%);
  --orange-gradient: linear-gradient(270deg, #ffb978 0%, #ff922d 100%);
  --blue: #4066ff;
  --lightBlue: #eeeeff;
  --shadow: 0px 23px 21px -8px rgba(136, 160, 255, 0.25);
}

body{
  background:  linear-gradient(90deg, #effcfc 0%, #ebfafc 50%, #fef8f8 100%);
}

button{
  cursor: pointer;
}

a{
  text-decoration: none;
  color: initial;
  cursor: pointer;
}

img, video {
  width: 100%;
  height: auto;
}

p {
  color: #494949;
  font-size: 13px;
  line-height: 22px;
}

h2, h3, h4{
  color: #000000;
}

h4 {
  font-weight: 1500;
}

h1 {
  font-family: 'Nunito Sans', sans-serif;
}


