.pList{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    padding:4rem 8rem;
  }
  
  .pListItem{
    flex:1;
    border-radius: 10px;
    cursor: pointer;
  }
  
  .pListImg{
    width: 100%;
    height: 150px;
    object-fit: cover;
  }
  
  .pListTitles>h1{
    font-size: 18px;
    color: #444;
  }
  
  .pListTitles>h2{
    font-size: 14px;
    font-weight: 400;
    color: #575757;
  }

  @media  screen and (max-width: 900px) {
    .pList {
        flex-direction: column;
        padding: 2rem 0;
    }
  }

  /* Animation */

  .slide-in-bottom-property {
    -webkit-animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 3s both;
            animation: slide-in-bottom 1.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) 3s both;
  }
  @-webkit-keyframes slide-in-bottom-property {
      0% {
        -webkit-transform: translateY(1000px);
                transform: translateY(1000px);
        opacity: 0;
      }
      100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
      }
    }
    @keyframes slide-in-bottom {
      0% {
        -webkit-transform: translateY(1000px);
                transform: translateY(1000px);
        opacity: 0;
      }
      100% {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
      }
    }