.Navbar {
  background: linear-gradient(90deg, #effcfc 0%, #ebfafc 50%, #fef8f8 100%);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 0vw 10vw;
}

.Nav-bar {
    display: flex;
    flex-direction: row;
}

.links {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 2rem;
    padding-right:6rem;
}

.links p{
    color: #000;
    font-size: 16px;
  :hover{
    color: #0d83d1;
  }
}

.Nav-btn{
    background: linear-gradient(216deg, #0E75B4 0%, #0393CA 50%, #002F99 100%);
    color: #000;
    font-size: 17px;
    text-transform: uppercase;
    border: none;
    padding: 10px 15px;
    border-radius: 10px;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.308);
    
    &:hover{
      color: #ffffff;
    }
}
  


  .menu{
    display: none;
    flex-direction: row-reverse;
    position: relative;
  }


  .menu-list {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    text-align: end;
    gap: 2rem;
    padding: 2rem;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    border-radius: 5px;
    :hover{
      color: #0d83d1;
    }
  }
  .menu-list p{
    font-size: 20px;
  }
  
  @media  screen and (max-width: 1050px){
    .links {
        display: none;
    }

    .Nav-btn_div{
        display: none;
    }

    .menu {
        display: flex;
    }
 }




